import React, { ChangeEvent } from 'react';
import { useDispatch } from 'react-redux';

export { OrderByBox };

function OrderByBox() {
    const dispatch = useDispatch();

    function handleChange(event: ChangeEvent<HTMLSelectElement>) {
        dispatch({
            type: 'postSlice/SEARCH_ORDER',
            ORDERBY: event.target.value,
        });
    }
    return (
        <>
            <label htmlFor="orderBySelect">Ï†ïÎ†¨</label>
            <select id="orderBySelect" onChange={handleChange} className="select-community-search">
                <option value="latest">ÏµúÏã†Ïàú</option>
                <option value="accuracy">Ï†ïÌôïÎèÑ</option>
                <option value="comment">ÎåìÍ∏Ä</option>
            </select>
        </>
    );
}
